import React from 'react';
import { Helmet } from 'react-helmet'

import Hero from '../components/Hero';
import Content from '../components/Content';
import me from '../assets/images/me.jpg';
import resume from '../assets/documents/Resume - Brendan Benner.pdf';

function HomePage(props) {
  return (
    <div>
      <Helmet><title>{props.title} | UC Berkeley</title></Helmet>
      <Hero title={props.title} subTitle={props.subTitle} text={props.text} />

      <Content>
        {/*<center><img src={me} className="me" alt="Me" /></center>*/}
        <p>I'm a fourth-year undergraduate at University of California, Berkeley seeking a Bachelor of Arts in Computer Science and Applied Mathematics. I am interested in software engineering in addition to systems programming and computer architecture. I have experience developing with Java, Python, C, SQL, and JavaScript, along with low-level languages such as RISC-V and Verilog HDL.</p>

        <p>This summer I had the opportunity to intern as a software engineer intern at AWS and work on their service CloudTrail. As part of my intern project, I employed my object-oriented programming skills in Python and TupeScript along with my knowledge of distributed systems in the cloud to develop a project that processes hundreds of thousands of customer API calls per day. As part of this experience, I developed strong fundamentals in maintaining and deploying to a CD pipeline, code reviews, design, team stand-ups, and collaboration in an industry environment.</p>

        <p>I am also a motivated and diligent person who can work both by myself and with others. I am open to full-time positions and am eager to put my skills to use. Please do not hesitate to contact me with any questions using my contact information.</p>
        <br />
        <center><a href={resume} target="_blank" className="display-5 font-weight-light nounderline">Resume</a></center>
        <br />
        <h3>Contact Information</h3>
        <a href="tel:8182599942">Phone: (818) 259-9942</a>
        <br />
        <a href="mailto: brendanrbenner@gmail.com">Email: brendanrbenner@gmail.com</a>
        <br />
        <a href="https://github.com/bbenner7635">GitHub</a>
      </Content>
    </div>
  );
}

export default HomePage;
