import React from 'react';

import Card from '../components/Card';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';


import storm from '../assets/images/storm2.png';
import ecodatalab from '../assets/images/ecodatalab.png';
import textsummarizer from '../assets/images/textsummarizer.png';
import click from '../assets/images/click.png';
import zynq from '../assets/images/zynq.jpg';

class Carousel extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      items: [
        {
          id: 0,
          title: '100MHz 5-Stage RISC-V CPU',
          subTitle: 'Verilog CPU w/ BIOS, UART, & branch prediction',
          imgSrc: zynq,
          link: 'https://drive.google.com/file/d/1KdaQ74oHtceNJSImDpH8TROFOOra0q8F/view?usp=sharing',
          selected: false
        },
        {
          id: 1,
          title: 'Cool Campus Challenge Pledges',
          subTitle: 'JavaScript, React, React Redux',
          imgSrc: ecodatalab,
          link: 'https://www.coolcampuschallenge.org/',
          selected: false
        },
        {
          id: 2,
          title: 'Algorithmic Text Summarizer',
          subTitle: 'Java (Data Structures & Algorithms)',
          imgSrc: textsummarizer,
          link: 'https://github.com/bbenner7635/Text-Summarizer',
          selected: false
        },
        {
          id: 3,
          title: 'Storm: Messenging Bot',
          subTitle: 'Python',
          imgSrc: storm,
          link: 'https://github.com/bbenner7635/Storm',
          selected: false
        },
      ]
    }
  }

  handleCardClick = (id, card) => {
    let items = [...this.state.items];

    items[id].selected = items[id].selected ? false : true;

    items.forEach(item => {
      if(item.id !== id) {
        item.selected = false;
      }
    });

    this.setState({
      items
    });
  }

  makeItems = (items) => {
    return items.map(item => {
      return <Card item={item} click={(e => this.handleCardClick(item.id, e))} key={item.id} />
    });
  }

  render() {
    return (
      <Container fluid={true}>
        <Row className="justify-content-around">
          {this.makeItems(this.state.items)}
        </Row>
      </Container>
    );
  }

}

export default Carousel;
